"use client";

import { useEffect, useState } from "react";
import { io } from "socket.io-client";
import {
  Heart,
  AlertCircle,
  Users,
  Droplet,
  TrendingDown,
  LogOut,
} from "lucide-react";
import Link from "next/link";
import HospitalMap from "@/components/HospitalMap";
import BloodCompatibilityChart from "@/components/blood-compatibility-chart";
import AIChatbot from "@/components/ai-chatbot";

export default function HospitalDashboard() {
  const [hospitalName, setHospitalName] = useState("Hospital");
  const [requests, setRequests] = useState<any[]>([]);
  const [activeDonors, setActiveDonors] = useState<any[]>([]);
  const [stats, setStats] = useState({ totalRequests: 0, livesSaved: 0, donorsNearby: 0, avgMatchTime: 0 });

  useEffect(() => {
    const user = localStorage.getItem("user");
    if (user) setHospitalName(JSON.parse(user).fullName);
  }, []);

  const loadStats = async () => {
    try {
      const r = await fetch("http://localhost:5000/hospital/stats");
      const d = await r.json();
      if (d.success) setStats((p) => ({ ...p, donorsNearby: d.donorsNearby, avgMatchTime: d.avgMatchTime }));
    } catch (err) {
      console.error(err);
    }
  };

  const loadActiveDonors = async () => {
    try {
      const r = await fetch("http://localhost:5000/hospital/active-donors");
      const d = await r.json();
      if (d.success) setActiveDonors(d.donors || []);
    } catch (err) {
      console.error(err);
    }
  };

  const loadRequests = async () => {
    try {
      const r = await fetch("/api/requests"); // or your real endpoint (REQUEST.getAllRequests)
      const d = await r.json();
      const reqs = d.requests || [];
      setRequests(reqs);
      setStats((prev) => ({ ...prev, totalRequests: reqs.length, livesSaved: Math.floor(reqs.length * 1.5) }));
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    loadStats();
    loadActiveDonors();
    loadRequests();

    const socket = io("http://localhost:5000");
    socket.on("donor_status_changed", () => {
      // reload when donor availability changes
      loadStats();
      loadActiveDonors();
    });

    return () => socket.disconnect();
  }, []);

  const getUrgencyColor = (urgency: string) => {
    switch (urgency?.toUpperCase()) {
      case "CRITICAL": return "bg-red-600 text-white";
      case "HIGH": return "bg-orange-500 text-white";
      case "MODERATE": return "bg-yellow-500 text-white";
      default: return "bg-green-500 text-white";
    }
  };

  const getStatusColor = (status: string) => {
    return status === "Pending"
      ? "bg-yellow-100 text-yellow-700"
      : status === "Fulfilled"
      ? "bg-green-100 text-green-700"
      : "bg-gray-200 text-gray-700";
  };

  return (
    <main className="min-h-screen bg-gradient-to-br from-white via-red-50 to-white">
      <header className="bg-gradient-to-r from-red-600 to-red-700 text-white p-6 sticky top-0 z-30 shadow-lg">
        <div className="max-w-7xl mx-auto flex justify-between items-center">
          <div className="flex items-center gap-3"><Heart className="w-8 h-8 fill-white" /><h1 className="text-3xl font-bold">{hospitalName} Dashboard</h1></div>
          <Link href="/" className="p-2 hover:bg-red-500 rounded-lg transition"><LogOut className="w-6 h-6" /></Link>
        </div>
      </header>

      <div className="max-w-7xl mx-auto p-6 space-y-8">
        <div className="bg-white rounded-xl shadow-lg overflow-hidden border-l-4 border-red-600">
          <div className="grid md:grid-cols-2 gap-6">
            <div className="p-8">
              <h2 className="text-3xl font-bold text-red-700">{hospitalName}</h2>
              <p className="text-gray-600">Real-time donor coordination</p>
              <div className="mt-4">
                <p>Active Requests: <b>{stats.totalRequests}</b></p>
                <p>Lives Saved: <b>{stats.livesSaved}</b></p>
              </div>
            </div>

            <div className="bg-gradient-to-br from-red-500 to-red-600 h-64 flex items-center justify-center relative overflow-hidden">
              <img src="/modern-hospital-medical-emergency-healthcare.jpg" alt="Hospital" className="w-full h-full object-cover" />
              <div className="absolute inset-0 bg-black bg-opacity-20 flex items-center justify-center"><Heart className="w-20 h-20 text-white opacity-80" /></div>
            </div>
          </div>
        </div>

        <div className="grid md:grid-cols-2 gap-6">
          <div className="bg-white p-6 rounded-xl shadow-lg">
            <h3 className="text-xl font-bold mb-2">Active Donors Nearby: {stats.donorsNearby}</h3>
            <div className="h-56 rounded overflow-hidden border">
              <HospitalMap activeDonors={activeDonors} />
            </div>
          </div>

          <div className="bg-white p-6 rounded-xl shadow-lg">
            <h3 className="text-xl font-bold mb-2">Quick Stats</h3>
            <div className="grid grid-cols-2 gap-4 mt-4">
              <div className="p-4 border rounded">Avg Match Time: <b>{stats.avgMatchTime} min</b></div>
              <div className="p-4 border rounded">Lives Saved: <b>{stats.livesSaved}</b></div>
            </div>
          </div>
        </div>

        <div className="grid md:grid-cols-4 gap-6">
          <div className="bg-gradient-to-r from-red-500 to-red-700 text-white rounded-xl shadow-lg p-6">
            <div className="flex justify-between items-center mb-2"><h3 className="text-sm font-semibold">Donors Nearby</h3><Users className="w-6 h-6" /></div><p className="text-3xl font-bold">{stats.donorsNearby}</p>
          </div>
          <div className="bg-gradient-to-r from-red-500 to-red-700 text-white rounded-xl shadow-lg p-6">
            <div className="flex justify-between items-center mb-2"><h3 className="text-sm font-semibold">Avg Match Time</h3><Droplet className="w-6 h-6" /></div><p className="text-3xl font-bold">{stats.avgMatchTime} min</p>
          </div>
          <div className="bg-gradient-to-r from-red-500 to-red-700 text-white rounded-xl shadow-lg p-6">
            <div className="flex justify-between items-center mb-2"><h3 className="text-sm font-semibold">Lives Saved</h3><TrendingDown className="w-6 h-6" /></div><p className="text-3xl font-bold">{stats.livesSaved}</p>
          </div>
          <div className="bg-gradient-to-r from-red-500 to-red-700 text-white rounded-xl shadow-lg p-6">
            <div className="flex justify-between items-center mb-2"><h3 className="text-sm font-semibold">Active Requests</h3><Heart className="w-6 h-6" /></div><p className="text-3xl font-bold">{stats.totalRequests}</p>
          </div>
        </div>

        <BloodCompatibilityChart />

        <div className="bg-white rounded-xl shadow-lg p-8 border-2 border-red-200">
          <h2 className="text-2xl font-bold mb-4">Active Blood Requests</h2>
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead><tr className="border-b"><th className="py-3 px-4">Blood Type</th><th className="py-3 px-4">Units</th><th className="py-3 px-4">Urgency</th><th className="py-3 px-4">Status</th></tr></thead>
              <tbody>
                {requests.map((req, i) => (
                  <tr key={i} className="border-b hover:bg-red-50">
                    <td className="py-4 px-4">{req.bloodType}</td>
                    <td className="py-4 px-4">{req.unitsNeeded}</td>
                    <td className="py-4 px-4"><span className={`px-3 py-1 rounded-full text-xs ${getUrgencyColor(req.urgency)}`}>{req.urgency}</span></td>
                    <td className="py-4 px-4"><span className={`px-3 py-1 rounded-full text-xs ${getStatusColor(req.status)}`}>{req.status}</span></td>
                  </tr>
                ))}
              </tbody>
            </table>
          </div>
        </div>

        <div className="bg-green-50 border-l-4 border-green-600 rounded-xl p-6 shadow-lg">
          <AlertCircle className="w-6 h-6 text-green-600" />
          <p className="text-green-800 font-semibold mt-2">AI is now using REAL active donors for matching.</p>
        </div>
      </div>

      <AIChatbot />
    </main>
  );
}
